import React from "react"
import { Parallax } from "react-spring/renderprops-addons.cjs"
import { animated } from "react-spring"
import Layout from "../components/layout"
import Hero from "../components/hero"
import Projects from "../components/projects"
import Menu from "../components/menu"
import About from "../components/about"
import Contact from "../components/contact"

const IndexPage = () => {
  let parallax;

  const scrollTo = (id) => {
    parallax.scrollTo(id);
  }
  
  return (<Layout>
    <Menu scrollTo={scrollTo} />
    <Parallax native ref={ref => (parallax = ref)}  pages={5}>
      <AnimatedHero offset={0} factor={1} />
      <AnimatedAbout offset={1} factor={1} />
      <AnimatedProjects offset={2} factor={2} />
      <AnimatedContact offset={4} factor={1} />
    </Parallax>
  </Layout>);
  //<GitHub offset={2} factor={1} />
}

const AnimatedHero = animated(Hero);
const AnimatedAbout = animated(About);
const AnimatedProjects = animated(Projects);
const AnimatedContact = animated(Contact);

export default IndexPage
